<template>
  <div>
    <MedyHeader :isFriendCampaign="true"/>
    <SideMenu :isOptinPage="true"/>
    <div class="mainArea overFlowCol" ref="mainArea">
      <pull-to :top-load-method="refresh">
        <div class="campaignMainArea">
          <div class="campaignTitle">{{campaign.campaignTitle}}</div>
          <div class="campaignInputArea">
            <div class="campaignPrompt">
              <span>紹介コード入力</span>
            </div>
            <div class="codeInput">
              <input type="text" id="campaignCodeInput" ref="campaignCodeInput"/>
            </div>
          </div>
          <div class="sendBtnArea">
            <div class="sendBtn">
              <span>
                送信する
              </span>
            </div>
          </div>
        </div>
        <div class="campaignIntro">
          <span>MEDYのサービスに関するお知らせや、キャンペーン情報を掲載しています。</span>
        </div>
      </pull-to>
    </div>
  </div>
</template>

<script>
import MedyHeader from '@/components/MedyHeader.vue'
import SideMenu from "@/components/SideMenu.vue"
import PullTo from '@/components/vue-pull-to/vue-pull-to.vue'

export default {
  name: 'friendCampaign',
  data() {
    return {
      campaignAreaCss: 'height:100%;margin-top:42px;',
      campaign: {
        campaignTitle: '1126_友人紹介'
      }
    };
  },
  components: {
    MedyHeader,
    SideMenu,
    PullTo,
  },
  mounted: function () {
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      //初期化
      this.$refs.mainArea.style.height = window.innerHeight - 42 + 'px';
    },
    refresh: function (loaded) {
      //TODO AJAX
      var _this = this;
      setTimeout(function () {
        _this.$refs.campaignCodeInput.value = '';
        loaded('done');
      }, 2000)
    },
  }
}
</script>
<style scoped>
.mainArea{
  margin-top:42px;
}
.mainArea .campaignMainArea{
  width: 100%;
  background-color: var(--white-two);
  display: inline-block;
  margin-top: 1px;
  padding-bottom: 24px;
}

.mainArea .campaignMainArea .campaignTitle{
  width: calc(100% - 12px);
  height: 36px;
  font-size: 18px;
  font-weight: 500;
  color: var(--black);
  line-height: 36px;
  padding-left: 12px;
  margin-top: 20px;
}

.mainArea .campaignMainArea .campaignInputArea{
  display: inline-block;
  position: relative;
  margin-top: 24px;
  padding-left: 12px;
  padding-right: 13px;
  width: calc(100% - 25px);
  margin-bottom: 32px;
}

.mainArea .campaignMainArea .campaignInputArea .campaignPrompt{
  padding: 8px 0 8px 0;
  height: 20px;
}

.mainArea .campaignMainArea .campaignInputArea .campaignPrompt span{
