  line-height: 20px;
  color: var(--black);
}

.mainArea .campaignMainArea .campaignInputArea .codeInput #campaignCodeInput{
  width: calc(100% - 12px);
  height: 30px;
  border-radius: 4px;
  background-color: var(--white-four);
