  display: inline-block;
}

.mainArea .campaignMainArea .sendBtnArea .sendBtn{
  width: 180px;
  height: 20px;
  border-radius: 16px;
  background-color: var(--dark);
  display: inline-block;
  position: relative;
  text-align: center;
  padding: 6px 0 6px 0;
}

.mainArea .campaignMainArea .sendBtnArea .sendBtn span{
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  color: var(--white-two);
}

.campaignIntro{
  width: calc(100% - 25px);
  padding: 10px 13px 12px 12px;
}

.campaignIntro span{
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: var(--black);
}
</style>
