import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  modelPageCount: 0,
  isPageBack: false,
  isMenuShow: false,
  isForbiddenPullDown: false,
  interestData: [],
  overSeasData: [],
  iPhoneWithoutTouch: false,
  globalBottomMargin: 0,
}

const mutations = {
  incrementModelPageCount (state) {
    state.modelPageCount++
  },
  decrementModelPageCount (state) {
    state.modelPageCount--
  },
  initModelPageCount (state) {
    state.modelPageCount = 1
  },
  clearModelPageCount (state) {
    state.modelPageCount = 0
  },
  saveIsPageBack (state, value) {
    state.isPageBack = value
  },
  showMenu (state) {
    state.isMenuShow = true;
  },
  hideMenu (state) {
    state.isMenuShow = false;
  },
  makeScrollForbiddenPullDown (state) {
    state.isForbiddenPullDown = true;
  },
  makeScrollCanPullDown (state) {
    state.isForbiddenPullDown = false;
  },
  setInterestArray (state, msg) {
    state.interestData = msg
  },
  setOverSeasArray (state, msg) {
    state.overSeasData = msg
  },
  detectIPhoneWithoutTouch (state) {
    var isIPhoneX = /iphone/gi.test(window.navigator.userAgent) && window.devicePixelRatio && window.devicePixelRatio === 3 && window.screen.width === 375 && window.screen.height === 812;
    var isIPhoneXSMax = /iphone/gi.test(window.navigator.userAgent) && window.devicePixelRatio && window.devicePixelRatio === 3 && window.screen.width === 414 && window.screen.height === 896;
    var isIPhoneXR = /iphone/gi.test(window.navigator.userAgent) && window.devicePixelRatio && window.devicePixelRatio === 2 && window.screen.width === 414 && window.screen.height === 896;

    var isStandAloneMode = ('standalone' in window.navigator) && (window.navigator.standalone);

    state.iPhoneWithoutTouch = (isIPhoneX || isIPhoneXSMax || isIPhoneXR) && isStandAloneMode;
    state.globalBottomMargin = state.iPhoneWithoutTouch ? 20 : 0;
  }
}

const getters = {
  canGoback: state => {
    return state.modelPageCount > 0;
  },
  gobackCount: state => {
    return 0 - state.modelPageCount;
  },
  shouldShowGoBack: state => {
    return state.modelPageCount > 1;
  },
  isShowMenu: state => {
    return state.isMenuShow;
  },
}

const actions = {
  showSideMenu({commit}){
    commit('showMenu')
  },
  hideSideMenu({commit}){
    commit('hideMenu')
  }
}

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})