<script>
import CommonModal from "@/components/cmptsModal/CommonModal.vue"

export default {
  name: "QADetailTop",
  props: {
  },
  components: {
    CommonModal
  },
  data() {
    return {
      showClipedMsg:false,
      hasCliped:false,
      modalClip:'clipMsg',
      articleInfo:{
        nMinute:"2018年11月21日",

        site:"MEDY臨床相談",
        title:"臨床Q&A Vol.363「テリパラチドを2年使用した患者の新規圧迫骨折の治タイトル全部表示",
        image:"http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
        question:"テリパラチドを2年使用した患者の新規圧迫骨折の治療について",
        job:"病院の勤務医整形外科 60代",
        answer:"テリパラチドはもう使用出来ないですが 皆さんは次に投与する薬剤は何をお使いですか？"
      }
    };
  },

  created: function () {
  },
  mounted: function () {
      this.hasCliped = false,
      this.showClipedMsg = false;
  },
  activated: function () {
    var path = window.location.origin+"/#"+this.$route.fullPath;
    var clipboard = new ClipboardJS('.clipBtn', {
        text: function() {
            return path;
        }
    })
  },
  computed:{
    facebookUrl:function(){
        return "https://www.facebook.com/sharer/sharer.php?u="+window.location.origin+"/%23"+this.$route.fullPath;
    },
    twitterUrl:function(){
        return "http://twitter.com/share?url="+window.location.origin+"/%23"+this.$route.fullPath;
    },
    lineUrl:function(){
        return "http://line.me/R/msg/text/?"+window.location.origin+"/%23"+this.$route.fullPath;
    }
  },
  methods: {
    close: function () {
      if (this.$store.getters.canGoback) {
        this.$router.go(this.$store.getters.gobackCount)
      } else {
        this.$router.push('/')
      }
    },
    showMask: function (event) {
      this.$emit('showMask', event);
    },
    clickClip:function(){
        var _this = this;
        if(!_this.hasCliped ){
            _this.hasCliped = true;
            // _this.showClipedMsg = true;
            // setTimeout(function () {
            //     _this.showClipedMsg = false;
            // }, 800);
        }else{
            _this.hasCliped = false
        }
    },
    clipMsgClose:function(){
        this.showClipedMsg = false;
    },
    
  }
};
</script>

