<template>
  <div class="cliplistArea">
    <div class="cliplistHeader">クリップリスト</div>
    <div class="cliplistPullCol">
      <pull-to ref="cliplistPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh">
        <div class="cliplistPrompt" v-if="isHaveData">
          <p>クリップした記事の一覧です。</p>
        </div>
        <div class="cliplistNoDataPrompt"  v-if="!isHaveData">
          <p>クリップされた記事がありません。</p>
          <p>興味ある記事を探して、いつでも読み直せるようにクリップしてみましょう</p>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="articleListCliplist" :isCliplist="true" :isTop="false" @setListScrollTop="setListScrollTop" v-if="isHaveData" @changeLoadingFlag="changeLoadingFlag"/>
          <pickUpArticles ref="pickUpArticles" :isCliplist="true" @setListScrollTop="setListScrollTop" v-if="!isHaveData"/>
        </div>
      </pull-to>
    </div>
    <MedyFooter/>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PickUpArticles from "@/components/cmptsClinical/PickUpArticles.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import MedyFooter from '@/components/MedyFooter.vue'

export default {
  name: 'medicalOpinionsList',
  components: {
    ArticleList,
    GoTopMark,
    MedyFooter,
    PickUpArticles,
    PullTo
  },
  data: function () {
    return {
      listScrollTop: 0,
      isTopMarkShow: false,
      isArticleLoading: false,
      isHaveData: true,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.cliplistPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      if(this.$route.params.pattern == "haveData"){
        this.isHaveData = true;
      } else {
        this.isHaveData = false;
      }
      this.$refs.articleListCol.style.height = window.innerHeight - 130 - this.$store.state.globalBottomMargin + 'px';
      this.isArticleLoading = false;
      this.isTopMarkShow = false;
      this.$refs.cliplistPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading && !this.isHaveData) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.articleListCliplist.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.cliplistPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.articleListCliplist.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function (value) {
      this.isArticleLoading = value;
    },
    refresh: function (loaded) {
      //pull down refresh
      if (this.isArticleLoading) {
        loaded("fail");
        return;
      }
      setTimeout(function(){
        loaded('done');
      },2000)
      // this.$refs.articleListCliplist.getArticleListByGenre(0, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.cliplistPull.getScrollPos();
    },
  }
}
</script>

<style scoped>
.cliplistArea .cliplistHeader {
  height: 42px;
  width: 100%;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  font-weight: 300;
  color: var(--black);
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  background-color: var(--white-two);
  line-height: 42px;
  position: fixed;
}

.cliplistArea .cliplistPullCol {
  margin-top: 42px;
  overflow: hidden;
  margin-bottom: 46px;
  background-color: var(--ice-blue);
}

.cliplistArea .cliplistPrompt{
  width: calc(100% - 48px);
  height: 18px;
  padding: 16px 36px 8px 12px;
  position: relative;
  display: inline-block;
  line-height: 17px;
}

.cliplistArea .cliplistNoDataPrompt{
  width: calc(100% - 26px);
  padding: 16px 14px 0 12px;
  position: relative;
  display: inline-block;
}

.cliplistArea .cliplistPrompt p{
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--black);
  line-height: 18px;
}

.cliplistArea .cliplistNoDataPrompt p{
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  line-height: 20px;
  margin-bottom: 12px;
}

.cliplistArea .cliplistNoDataPrompt p:last-child{
  margin-bottom: 4px;
}

.cliplistArea .articleListCol {
  position: relative;
}
</style>
